.box {
  width: auto;
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  -webkit-animation: mainBox 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
  -o-animation: mainBox 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
  animation: mainBox 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
  animation-delay: 5s;

  .title {
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    height: 50px;

    .block {
      width: 0%;
      height: inherit;
      background: #f6e8b1;
      position: absolute;
      animation: mainBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
      display: flex;
    }

    h1 {
      color: #fff;
      font-size: 1.8 rem;
      -webkit-animation: mainFadeIn 2s forwards;
      -o-animation: mainFadeIn 2s forwards;
      animation: mainFadeIn 2s forwards;

      animation-delay: 1.6s;
      opacity: 0;
      display: flex;
      align-items: baseline;
      position: relative;
      word-break: break-all;
      white-space: nowrap;

      span {
        width: 0px;
        height: 0px;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border-radius: 50%;

        background: #d4caa3;
        -webkit-animation: load 0.6s cubic-bezier(0.74, 0.06, 0.4, 0.92)
          forwards;
        animation: popIn 0.8s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
        animation-delay: 2s;
        margin-left: 5px;
        margin-top: -10px;
        position: absolute;
        bottom: 13px;
        right: -12px;
      }
    }
  }

  .name {
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    height: 30px;
    margin-top: -10px;

    .block {
      width: 0%;
      height: inherit;
      background: #c8bca8;
      position: absolute;
      animation: secBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
      animation-delay: 2s;
      display: flex;
    }

    p {
      animation: secFadeIn 2s forwards;
      animation-delay: 3.2s;
      opacity: 0;
      font-weight: 400;
      color: #ffffff;
      font-size: 1rem;
      text-transform: uppercase;
      letter-spacing: 5px;
    }
  }
}
.content {
  width: auto;
  height: 93%;
  transform: translateY(5rem);
  overflow: auto;
  line-height: 2rem;
  font-size: 1rem;
  padding: 0 2rem;
  color: #d7d7d7;
  -webkit-animation: contentFadeIn 1.5s cubic-bezier(0.74, 0.06, 0.4, 0.92)
    forwards;
  -o-animation: contentFadeIn 1.5s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
  animation: contentFadeIn 1.5s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;

  h2 {
    word-break: keep-all;
    white-space: break-spaces;
  }
}

/* Hide scrollbar for Chrome, Safari and Opera */
.content::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.content {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

@media (max-width: 800px) {
  .content {
    font-size: 0.5rem;
  }
}

@keyframes mainBox {
  0% {
    opacity: 1;
  }
  25% {
    opacity: 0;
  }
  50% {
    position: absolute;
    top: 2rem;
    left: 50%;
    transform: translateX(-50%);
  }
  100% {
    position: absolute;
    top: 2rem;
    left: 50%;
    transform: translateX(-50%);
    opacity: 1;
  }
}

@keyframes mainBlock {
  0% {
    width: 0%;
    left: 0;
  }
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}

@keyframes secBlock {
  0% {
    width: 0%;
    left: 0;
  }
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}

@keyframes mainFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes popIn {
  0% {
    width: 0px;
    height: 0px;
    background: #d4caa3;
    border: 0px solid #ddd;
    opacity: 0;
  }
  50% {
    width: 10px;
    height: 10px;
    background: #d4caa3;
    opacity: 1;
    bottom: 45px;
  }
  65% {
    width: 7px;
    height: 7px;
    bottom: 0px;
    width: 15px;
  }
  80% {
    width: 10px;
    height: 10px;
    bottom: 20px;
  }
  100% {
    width: 7px;
    height: 7px;
    background: #d4caa3;
    border: 0px solid #222;
    bottom: 5px;
  }
}

@keyframes secFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}

@keyframes contentFadeIn {
  0% {
    opacity: 0;
    display: flex;
  }
  100% {
    opacity: 1;
    display: flex;
  }
}
